.movieBox {
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  background-color: rgb(255, 245, 255);
  transition: 0.4s ease;
  max-width: 500px;
  min-height: 300px;
  font-size: 14px;
}
.movieBox:hover {
  transform: translateY(-10px);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.coverImg {
  position: relative;
  top: -60px;
  max-width: 150px;
  margin-right: 30px;
}

.title {
  font-weight: 700;
  text-decoration: none;
  margin-bottom: 0px;
  font-size: 23px;
}
.title a {
  color: black;
}
.title a:hover {
  text-decoration: underline;
}

.movieBox .year {
  margin-top: 0;
}

.movieBox .genres {
  display: flex;
  flex-wrap: wrap;
}
.movieBox .genres li {
  margin-right: 10px;
}

@media screen and (max-width: 1090px) {
  .movieBox {
    max-width: 700px;
  }
}
