.nav {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: transparent;
  display: flex;
  align-items: center;
  transition: 0.4s;
  justify-content: space-between;
  z-index: 9;
}
.nav:hover {
  background-color: rgb(124, 82, 186);
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px;
}
.logo {
  margin-left: 30px;
  font-size: 30px;
  color: rgb(249, 196, 255);
}
.icons {
  color: #fff;
  font-size: 23px;
  margin-right: 50px;
}
.icons a {
  margin-left: 20px;
  color: rgb(249, 196, 255);
}
