.backgroundImg {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  filter: brightness(50%);
}

.show {
  position: absolute;
  top: 20%;
  left: 20%;
  display: flex;
  align-items: center;
  z-index: 9;
  margin-right: 20%;
}
.contents {
  max-width: 500px;
}
.coverImg {
  margin-right: 30px;
}
.title {
  color: #fff;
  font-weight: 900;
}
.title:hover {
  text-decoration: underline;
}
.description {
  color: #fff;
}

.genres {
  color: #fff;
  list-style: none;
  margin-right: 10px;
}
.info {
  margin-right: 10px;
  font-weight: 700;
}
