* {
  box-sizing: border-box;
}

ul {
  list-style: none;
  padding-left: 0px;
}

a {
  text-decoration: none;
}

body {
  background-image: linear-gradient(
    to right top,
    #f9f871,
    #ffe469,
    #ffd166,
    #ffbe66,
    #ffac69,
    #ff9d6d,
    #ff8f73,
    #ff827b,
    #fc7486,
    #f46993,
    #e861a2,
    #d65db1
  );
  height: 100%;
}
